*{
  box-sizing: border-box;
}

:root {
  --background-color: rgb(31,31,31);
  --blue: #58abf6;
  --background-color-top: rgb(43, 43, 43);
}

.input{
  position: relative;
  cursor: text;
  font-size: 14px;
  line-height: 20px;
  padding: 0 16px;
  width: 190px;
  background-color: #fff;
  border: 1px solid #d6d6e7;
  border-radius: 15px;
  height: 35px;
  color: rgb(35, 38, 59);
  box-shadow: inset 0 1px 4px 0 rgb(119 122 175 / 30%);
  overflow: hidden;
  transition: all 100ms ease-in-out;
}

fieldset {
  border: none;
}

#Filter select:focus, #Filter input:focus{
  outline: none;
}

.input:focus{
  outline: none;
}

.dropdown-content::-webkit-scrollbar, body::-webkit-scrollbar{
  width: 2px;
}

.dropdown-content::-webkit-scrollbar, body::-webkit-scrollbar-thumb{
  background: #58abf6;
  background: var(--blue);
  border-radius: 20px;
  overflow: hidden;
}

/* body::-webkit-scrollbar-button {
  width: 100px;
  height: 300px;
} */

body {
  background-color: #000;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,h2,h3,h4,p{
  margin: 0;
  font-weight: 500;
}

ul{
  padding: 0
}

li{
  list-style-type: none;
}

select{
  border: 1px solid #d6d6e7;
}

#mobile-visible {
  display: flex;
}

#desktop-visible{
  display: none
}

main{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  margin: 0px;
  padding: 0px;
  width: 100%;
  height: 100%;
  max-width: 100vw;
  min-height: 100vh;
  box-sizing: border-box;
  text-align: center;
}

section{
  margin: 0;
  padding: 1em;
  width: 100%;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

@media (min-width: 1000px) {
  #mobile-visible {
    display: none;
  }
  
  #desktop-visible{
    display: flex;
  }
}

#Landing{
    min-height: 100vh;
    color: #fff;
    min-width: 100%;
    background-image: linear-gradient(to right bottom, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1);  font-weight: 400;
    padding: 0 16%;
}

#Landing nav{
    justify-content: space-between;
    margin-top: 2em;
}

.dog{
    width: 100%;
    -webkit-filter: invert(100%);
            filter: invert(100%);
    max-width: 5em;
}

#blob{
    position: absolute;
    bottom: 0em;
    right: 10em;
    width: 35em;
}

#flex-landing{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

#left-side-landing{
    margin-bottom: 2em;
}

#rigth-side-landing, #left-side-landing{
    width: 100%;
    flex: 1 1;
}

#left-side-landing{
    display: flex;
    height: auto;
    flex-direction: column;
    align-items: center;
}

#left-side-landing p{
    margin: 1em 0;
}

#left-side-landing h1{
    font-size: 50px;
    width: 100%;
    font-family: 'Lobster', cursive;
}

#button-landing{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0.5em;
    width: 100%;
    max-width: 200px;
    color: #fff;
    border-radius: 30px;
    border: 1px solid #fff;
    transition: all 0.3s;
}

#button-landing p{
    margin: 0
}

#button-landing:hover{
    box-shadow: 0px 0px 23px 0px rgba(255,255,255,0.75);
}

#button-landing a:hover ~ #left-side-landing h1{
    color: violet;
}

#rigth-side-landing figure{
    display: flex;
    width: 100%;
    border-radius: 95% 15% 95% 15%;
    align-items: center;
    justify-content: center;
    border: 2px solid #fff;
    background-size: 300% 300%;
    margin: 0;
    background-image: linear-gradient(
        -45deg, 
        rgba(59,173,227,1) 0%, 
        rgba(87,111,230,1) 25%, 
        #aa8fd8 51%, 
        #e058a5 100%
    ); 
  -webkit-animation: AnimateBG 10s ease infinite; 
          animation: AnimateBG 10s ease infinite;
}

#rigth-side-landing img{
    width: 100%;
    max-width: 8em;
    z-index: 10;
}

#logos{
    display: flex;
    position: relative;
    top: 20px;
    margin: 1em 2em;
    width: 100px;
    flex-direction: row;
    justify-content: space-between;
}

#logos a {
    border: none;
    fill: #fff;
    width: 40px;
    display: flex;
    justify-content: flex-end;
}

#logos a:hover{
    cursor: pointer;
}

#logos svg{
    width: 30px;
}

#flex-landing-height {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 100%;
} 

@-webkit-keyframes AnimateBG { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
} 

@keyframes AnimateBG { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}

