.metronome {
  --uib-size: 100px;
  --uib-speed: 1.6s;
  --uib-color: #fff;
  position: relative;
  display: flex;
  top: 10em;
  align-items: center;
  justify-content: flex-start;
  height: var(--uib-size);
  width: var(--uib-size);
}

.metronome__dot {
  position: absolute;
  top: 13.5%;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
  width: 100%;
  -webkit-animation: swing var(--uib-speed) linear infinite;
          animation: swing var(--uib-speed) linear infinite;
}

.metronome__dot::before {
  content: '';
  height: 25%;
  width: 25%;
  border-radius: 50%;
  background-color: var(--uib-color);
}

.metronome__dot:nth-child(1) {
  -webkit-animation-delay: calc(var(--uib-speed) * -0.36);
          animation-delay: calc(var(--uib-speed) * -0.36);
}

.metronome__dot:nth-child(2) {
  -webkit-animation-delay: calc(var(--uib-speed) * -0.27);
          animation-delay: calc(var(--uib-speed) * -0.27);
  opacity: 0.8;
}

.metronome__dot:nth-child(2)::before {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.metronome__dot:nth-child(3) {
  -webkit-animation-delay: calc(var(--uib-speed) * -0.18);
          animation-delay: calc(var(--uib-speed) * -0.18);
  opacity: 0.6;
}

.metronome__dot:nth-child(3)::before {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

.metronome__dot:nth-child(4) {
  -webkit-animation-delay: calc(var(--uib-speed) * -0.09);
          animation-delay: calc(var(--uib-speed) * -0.09);
  opacity: 0.4;
}

.metronome__dot:nth-child(4)::before {
  -webkit-transform: scale(0.7);
          transform: scale(0.7);
}

@-webkit-keyframes swing {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  15% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  65% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes swing {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  15% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  65% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
  
